import { resetPayPassWord , getCodeByPhone } from '@/utils/ApiList.js'
import { mapState } from 'vuex'
import baseUrl from '@/utils/url.js'
const app = getApp()
export default {
    data() {
        return {
            bg:this.imgs.loginBg,
            account: '',
            passwordF:'',
            passwordS: '',
            phone: '',
            smsCode:'',
            cntNum:119,
            isShowNumber: false,
            timer:0,
			cardId: app.globalData.cardId
            // card_no: window.localStorage.getItem('CARD_ID') || '',      //卡号
            // open_id: window.localStorage.getItem('OPEN_ID') || ''       //openid
        }
    },
    mounted () {
        clearInterval(this.timer)
        this.timer=0;
    },
    methods: {
        setPassword: function() {
			console.log(baseUrl)
            let that = this
            if (that.cardId=='') {
                that.$router.push('/change')
            }else {
                if (that.smsCode ==''){
                    uni.showToast({
						title:'请输入验证码',
						icon:'none'
					})
                }else {
					uni.request({
						url: baseUrl+'wx/modify_pay_password',
						data: {
							card_no: that.cardId,
							pay_password: that.passwordF,
							phone_number: that.phone,
							sms_code: that.smsCode
						},
						method:'POST',
						success:res=>{
							console.log(res)
							if (res.statusCode == 200) {
								uni.showToast({
									title: res.data.msg,
									icon:'none'
								})
								let t=setTimeout(()=>{
									uni.navigateBack()
									clearTimeout(t)
								},2000)
							}else {
								uni.showToast({
									title: res.errMsg,
									icon:'none'
								})
							}
						},
					})
      //               resetPayPassWord({
      //                   card_no: that.cardId,
      //                   pay_password: that.passwordF,
      //                   phone_number: that.phone,
      //                   sms_code: that.smsCode
      //               }).then(res => {
						// console.log(res)
      //                   if (res.statusCode == 200) {
      //                       uni.showToast({
      //                       	title: res.data.msg,
      //                       	icon:'none',
      //                       	complete: action=>{
      //                       		uni.navigateBack()
      //                       	}
      //                       })
      //                   }else {
      //                       uni.showToast({
      //                       	title: res.errMsg,
      //                       	icon:'none'
      //                       })
      //                   }
      //               })
                }
               
            }
        },
        checkPhone:function () {
            if (!(/^1[345678]\d{9}$/).test(this.phone)){
                uni.showToast({
                	title:'手机号格式不正确',
                	icon:'none'
                })
            }
        },
        getSmsCode: function () {
            if (this.phone=='') {
			    uni.showToast({
					title:'请输入手机号',
					icon:'none'
			    })
            }else {
				uni.request({
					url: baseUrl+'wx/send_sms_code',
					data: {
						phone_number:this.phone
					},
					method:'POST',
					success:res=>{
						if (res.statusCode == 200) {
						    uni.showToast({
						    	title:'验证码发送成功',
						    	icon:'none'
						    })
						    this.isShowNumber = true
						    this.timer = setInterval(this.cntNumDown,1000)
						}
					},
				})
                // getCodeByPhone({
                //     phone_number:this.phone
                // }).then(res => {
                //     console.log(res)
                //     if (res.statusCode == 200) {
                //         uni.showToast({
                //         	title:'验证码发送成功',
                //         	icon:'none'
                //         })
                //         this.isShowNumber = true
                //         this.timer = setInterval(this.cntNumDown,1000)
                //     }
                // })
            }
        },
        cntNumDown:function () {
            if (this.cntNum>0){
                this.cntNum-=1
            }else {
                this.cntNum=119 
                this.isShowNumber=false
                clearInterval(this.timer)
                this.timer = 0
            }            
        },
        check: function () {
            if (this.passwordS != this.passwordF) {
                uni.showToast({
                	title:'两次输入不一致，请重新输入',
                	icon:'none'
                })
            }
        }
    },
}
